<template>
	<div class="setting-container">
		<div class="header">
			<div class="header-media">
				<img class="user-avatar" :src="userInfo.avatarUrl" alt="">
			</div>
			<van-uploader :after-read="afterRead" class="header-setting">
				<van-button icon="plus" round plain size="small" type="default">修改头像</van-button>
			</van-uploader>
		</div>

		<div class="main">
			<div class="group balance-group">
				<van-form @submit="onSubmit">
					<van-field v-model="userInfo.nick" name="昵称" label="昵称" placeholder="昵称" />
					<van-field readonly clickable name="picker" :value="value" label="城市" placeholder="点击选择城市" @click="showPicker = true" />
					<van-popup v-model="showPicker" position="bottom">
						<van-area title="选择城市" :area-list="areaList" :columns-num="2" @confirm="onConfirm" @cancel="onClose" />
					</van-popup>
					<div style="margin: 16px;">
						<van-button block type="info" native-type="submit">
							提交
						</van-button>
					</div>
				</van-form>
			</div>

		</div>
	</div>
</template>

<script>
	import util from '@/common/util/util'
	import areaList from '@/common/area/area'
	import {
		isEmpty
	} from '@/common/validate/validate'
	export default {
		data() {
			return {
				userInfo: {},
				value: '',
				areaList,
				showPicker: false,
			}
		},
		created() {
			this.getUserDetail();
		},
		methods: {
			getUserDetail() {
				this.$request.get('/user/detail', {
					token: util.storage.get('token')
				}).then(res => {
					// console.log(res);
					if (res.code !== 0) {
						return;
					}
					const baseInfo = res.data.base
					this.userInfo = {
						...res.data.base,
						avatarUrl: baseInfo.avatarUrl || `${require('@/assets/avatar_default.png')}`,
						nick: baseInfo.nick || `${baseInfo.sourceStr}${baseInfo.id}`,
					}
					this.value = res.data.base.province + ' ' + res.data.base.city;
				})
			},
			onSubmit(values) {
				let avatarUrl = this.userInfo.avatarUrl;
				if (isEmpty(this.userInfo.nick)) {
					this.$toast('昵称不能为空')
					return
				}
				if (avatarUrl.includes('avatar_default') || avatarUrl.includes('base64')) {
					console.log('默认头像')
					avatarUrl = ''
				}
				this.$toast.loading({
					mask: true,
					message: '提交中...',
					duration: 0
				});
				this.updateUserInfo(avatarUrl, this.userInfo.city, this.userInfo.nick, this.userInfo.province)
			},
			updateUserInfo(avatarUrl, city, nick, province) {
				this.$request.post('/user/modify', {
					avatarUrl: this.userInfo.avatarUrl,
					city: this.userInfo.city,
					nick: this.userInfo.nick,
					province: this.userInfo.province,
					token: util.storage.get('token')
				}).then(res => {
					console.log(res);
					if (res.code !== 0) {
						this.$toast(res.msg);
						return;
					}
					this.$store.commit('updateUserInfo', this.userInfo);
					this.$toast('资料修改成功');
					this.$router.go(-1);
				},err=>{
					this.$toast.clear();
				});
			},
			onConfirm(value) {
				// console.log(value);
				// this.value = value;
				var txt = '';
				value.forEach(e => {
					txt += e.name + ' ';
				});
				this.value = txt;
				this.userInfo.province = value[0].name;
				this.userInfo.city = value[1].name;
				this.showPicker = false;
			},
			onClose() {
				this.showPicker = false;
			},
			afterRead(file) {
				// console.log(file);
				this.$toast.loading({
					mask: true,
					message: '上传中...',
					duration: 0
				});
				const formData = new FormData();
				formData.append('upfile', file.file, 'avatar.jpg')
				formData.append('token', util.storage.get('token'))
				this.$request.uploadFile('/dfs/upload/file', formData).then(res => {
					console.log(res);
					if (res.code !== 0) {
						this.$toast(res.msg);
						return;
					}
					this.userInfo.avatarUrl = res.data.url;
					this.$toast('上传图片成功');
				},err=>{
					this.$toast('上传失败，请使用1M以内的图片');
				})
			}
		}
	}
</script>

<style lang="less" scoped>
	.style-box() {
		box-sizing: border-box;
		border-radius: 8px;
		overflow: hidden;
		background: #fff;
	}

	.setting-container {
		width: 100%;
		height: 100vh;
	}

	.header {
		box-sizing: border-box;
		width: 100%;
		display: flex;
		justify-content: space-between;
		align-items: center;
		height: 180px;
		color: #fff;
		padding: 0 15px;
		// background-color: #fe4140;
		background: linear-gradient(309deg,
			rgba(255, 112, 61, 1) 0%,
			rgba(255, 70, 57, 1) 100%);

		&-media {
			width: 80px;
			height: 80px;
			border-radius: 50%;
			overflow: hidden;
			margin-right: 10px;

			.user-avatar {
				width: 100%;
				height: 100%;
			}
		}

		&-title {
			font-size: 22px;
		}

		&-setting {
			// position: absolute;
			// top: 30px;
			// right: 30px;
			font-size: 22px;
			// margin-left: 62vw;
		}
	}

	.main {
		width: 100%;
		height: 100%;
		// padding:0 20px;
		margin-top: -50px;
		// background-color: ;
		background-color: rgba(0, 0, 0, .03);
	}

	.group {
		.style-box();
		margin: 15px 15px 15px 15px;

		&-inner {
			padding: 10px 0;
		}
	}

	.balance-group {
		.style-box();

		.van-row {
			text-align: center;
		}

		.van-col {
			text-align: center;
		}

		&-title {
			font-size: 30px;
			font-weight: bold;
			color: #000;
			margin-bottom: 5px;
			margin-top: 10px;
		}

		&-txt {
			font-size: 12px;
			// color: #999;
		}
	}

	.changy-group {
		.van-icon {
			font-size: 24px;
		}

		.van-col {
			margin-bottom: 10px;
		}
	}

	.user-gruop {
		&-name {
			// padding: 10px 0;
			color: #333;
			font-size: 12px;
			margin-bottom: 10px;
		}
	}

	.user-section {
		.style-box();
		margin: 0 20px 15px 20px;

		&-title {
			font-size: 14px;
			color: #000;
			font-weight: bold;
		}

		&-hd {
			box-sizing: border-box;
			width: 100%;
			padding: 10px 0px;
			overflow: hidden;
			color: #323233;
			font-size: 14px;
			line-height: 24px;
			background-color: #fff;
			display: flex;
			align-items: center;
		}

		&-bd {
			box-sizing: border-box;
			width: 100%;
			padding: 15px 0;
		}

		&-grid {
			box-sizing: border-box;
			width: 20%;
			float: left;
			text-align: center;

			.van-icon {
				font-size: 24px;
				margin-bottom: 5px;
			}

			&-img {
				display: inline-block;
				width: 30px;
				height: 30px;
				// margin-bottom: 5px;
			}

			&-title {
				color: #333;
				font-size: 12px;
			}
		}
	}
</style>
